  0% {
    transform: scale(0);
    opacity: 0;
  }
  15% {
    transform: scale(0.5);
    opacity: 1;
  }
  50% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(3);
  }
}

