  margin-top: 5px;
}

.project-page .project-detail {
  margin-top: 20px;
}

.project-page .project-detail .project-detail-label {
  color: #555555;
  text-transform: uppercase;
  font-size: 0.9em;
}

.project-page .project-detail .project-detail-value {
  font-weight: bold;
}

.project-page .project-naviagtion {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  font-size: 26px;
}

.project-page .project-naviagtion a {
  margin: 0 60px;
  text-align: center;
}

@media all and (max-width: 767px) {
  .project-page .project-description {
    font-size: 14px;
  }
}

/**
 * COUNTERS
 */
.paralax-counters {
  background: #000000 no-repeat fixed 50% 50%/cover;
  position: relative;
}

.paralax-counters:after {
  z-index: 10;
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  content: '';
  background: rgba(0, 0, 0, 0.6);
}

.Counter {
  color: #ffffff;
  text-align: center;
  position: relative;
  z-index: 20;
}

.Counter__icon {
  font-size: 24px;
}

.Counter__number {
  font-size: 48px;
  font-weight: bold;
  margin: 10px 0;
}

.Counter__text {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #ffffff;
}

@media all and (max-width: 767px) {
  .Counter:nth-of-type(1),
  .Counter:nth-of-type(2) {
    margin-bottom: 30px;
  }
}

/**
 * Blog
 */
.Blog__post {
  position: relative;
  overflow: hidden;
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -ms-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.Blog__post:after {
  content: '';
  z-index: 10;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  top: 0;
  left: 0;
  transform: translateY(0);
  -webkit-transition: all 0.6s cubic-bezier(1, 0.5, 0.8, 1);
  -moz-transition: all 0.6s cubic-bezier(1, 0.5, 0.8, 1);
  -ms-transition: all 0.6s cubic-bezier(1, 0.5, 0.8, 1);
  -o-transition: all 0.6s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.6s cubic-bezier(1, 0.5, 0.8, 1);
}

.Blog__post:hover {
  z-index: 10;
  transform: scale(1.1);
}

.Blog__post:hover:after {
  opacity: 0;
}

.Blog__post:hover img {
  filter: grayscale(75%);
}

.Blog__post:hover .Blog__post-caption {
  opacity: 1;
  transition-delay: 0.4s;
}

.Blog__post-image a {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  z-index: 15;
}

.Blog__post-image img {
  width: 100%;
  height: auto;
  filter: grayscale(100%);
  -webkit-transition: all 0.6s ease-out;
  -moz-transition: all 0.6s ease-out;
  -ms-transition: all 0.6s ease-out;
  -o-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
}

.Blog__post-caption {
  background: rgba(0, 0, 0, 0.4);
  z-index: 20;
  opacity: 0;
  color: #ffffff;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 20px 30px;
  -webkit-transition: all 0.4s cubic-bezier(1, 0.5, 0.8, 1);
  -moz-transition: all 0.4s cubic-bezier(1, 0.5, 0.8, 1);
  -ms-transition: all 0.4s cubic-bezier(1, 0.5, 0.8, 1);
  -o-transition: all 0.4s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.4s cubic-bezier(1, 0.5, 0.8, 1);
}

.Blog__post-caption h4 {
  font-size: 16px;
  letter-spacing: 2px;
  color: #ffffff;
  text-transform: uppercase;
  line-height: 1.5em;
}

.Blog__post-categories {
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -ms-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  margin-top: 20px;
  list-style-type: none;
  padding-left: 0px;
}

.Blog__post-categories:before {
  content: '';
  width: 30px;
  height: 1px;
  vertical-align: middle;
  background: #C9C9C9;
  display: inline-block;
  margin-right: 15px;
}

.Blog__post-categories li {
  display: inline;
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-left: 3px;
  color: #C9C9C9;
}

@media all and (max-width: 767px) {
  .Blog__post {
    margin-bottom: 10px;
    z-index: 10;
  }
  .Blog__post:after {
    opacity: 0;
  }
  .Blog__post .Blog__post-caption {
    opacity: 1;
  }
  .Blog__post:hover {
    transform: inherit;
  }
}

/**
 * BLOG POST
 */
.blog-post-image {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
}

.blog-post-image img {
  max-width: 100%;
}

.blog-post-meta-detail {
  text-transform: uppercase;
  font-weight: bold;
}

.NavLinks {
  width: 750px;
  margin: 50px auto;
  display: flex;
  justify-content: center;
}

.NavLinks__link {
  width: 50%;
  display: flex;
  flex-direction: column;
  padding: 10px 50px;
}

.NavLinks__link.--previous {
  align-items: flex-end;
  border-right: 1px solid #000000;
  text-align: right;
}

.NavLinks__link.--next {
  align-items: flex-start;
  border-left: 1px solid #000000;
}

.NavLinks__label {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 2px;
  margin-bottom: 20px;
}

.NavLinks__title {
  font-size: 22px;
}

.NavLinks__title a {
  color: #000000;
}

@media all and (max-width: 767px) {
  .blog-post-text,
  .NavLinks {
    width: auto;
  }
  .blog-post-text__title,
  .NavLinks__title {
    font-size: 16px;
  }
  .blog-post-text__link.--next,
  .NavLinks__link.--next {
    padding: 10px 0 10px 20px;
  }
  .blog-post-text__link.--previous,
  .NavLinks__link.--previous {
    padding: 10px 20px 10px 0;
  }
}

/**
 * TESTIMONIALS
 */
.section.paralax-testimonials {
  background: #000000 no-repeat fixed 50% 50%/cover;
  position: relative;
  padding: 100px 0 80px 0;
}

.section.paralax-testimonials:after {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  content: '';
  background: rgba(0, 0, 0, 0.6);
}

.Testimonials .owl-dots {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.Testimonials .owl-dots .owl-dot span {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1px solid #ffffff;
  margin: 5px;
  display: inline-block;
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -ms-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

.Testimonials .owl-dots .owl-dot.active span {
  background: #ffffff;
}

.Testimonial {
  color: #ffffff;
}

.Testimonial__author {
  text-transform: uppercase;
  margin-right: 40px;
  color: #C9C9C9;
}

.Testimonial__author:before {
  background-color: #C9C9C9;
}

.Testimonial__body {
  font-size: 16px;
}

.Testimonial__body p {
  color: #ffffff;
}

.Testimonial i {
  font-size: 48px;
  margin-bottom: 10px;
}

/**
 * CONTACT
 */
.Contact__detail {
  margin-top: 20px;
  display: flex;
  align-items: center;
}

.Contact__detail i {
  font-size: 1em;
  width: 50px;
  height: 50px;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000000;
  color: #ffffff;
}

@media all and (max-width: 767px) {
  .Contact h4 {
    margin-top: 50px;
  }
}

/**
 * FOOTER
 */
footer {
  text-align: center;
  color: #C9C9C9;
  background: #000000;
  padding: 60px 0;
}

footer .footer-contact {
  text-align: left;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-size: 12px;
}

footer .footer-contact i {
  font-size: 18px;
  margin-right: 15px;
}

footer .copyrights {
  font-family: "Montserrat";
  font-size: 10px;
  letter-spacing: 2px;
  margin-top: 10px;
}

footer .footer-socials {
  text-align: right;
}

footer .footer-socials .fa {
  font-size: 22px;
  margin-left: 5px;
}

footer .footer-socials a {
  color: inherit;
}

@media all and (max-width: 767px) {
  footer .footer-contact {
    justify-content: center;
  }
  footer .footer-contact i {
    margin-right: 5px;
  }
}

@media all and (max-width: 991px) {
  .offer-wrap {
    margin-bottom: 30px;
  }
}

@media all and (max-width: 767px) {
  section .section--basic,
  .section .section--basic {
    padding: 60px 0;
  }
  section .section--basic .headline,
  .section .section--basic .headline {
    margin-bottom: 60px;
  }
  section .section--basic .headline h2,
  .section .section--basic .headline h2 {
    text-transform: uppercase;
    font-weight: bold;
  }
  section .section--basic .headline--no-margin-bottom,
  .section .section--basic .headline--no-margin-bottom {
    margin-bottom: 0px;
  }
  .Intro .IntroTyping__body {
    font-size: 2em;
  }
}

/**
 * CONTACT FORM
 */
.Form {
  background: #000000;
  padding: 40px;
  color: #ffffff;
  font-size: 14px;
}

.Form__group {
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
}

.Form__group.Form--error {
