}

.Form__label {
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 10px;
  font-family: "Montserrat";
}

.Form ::selection {
  background: rgba(255, 255, 255, 0.15);
}

.Form__success {
  color: #ffffff;
  text-align: center;
  padding: 20px;
  background: #00d1b2;
}

.Form__error {
  color: #ffffff;
  text-align: center;
  padding: 20px;
  background: #D10000;
}
